/*************************
GENERAL STYLING
**************************/
.settings--main {
  width: 100%;
  margin-top: 4.62em;
  background-color: var(--color-white);
  padding: 4em 1em;
  font-family: "Poppins", sans-serif;
}
.settings__heading__container {
  text-align: center;
  margin-bottom: 3.5em;
}
.settings__heading__container h1 {
  font-family: "Nunito", cursive;
  margin-bottom: 0.2em;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  font-size: 2.1em;
  line-height: 1.15;
  color: var(--h1-color);
  text-shadow: 0 0 3px rgba(0, 0, 0, 0.35);
}
.settings__heading__container p {
  color: var(--nav-heading-color);
  font-size: 1em;
}

.message {
  font-size: 1.1em;
  color: var(--info-color);
}
.message--container {
  position: absolute;
  top: 4.7em;
  left: 0;
  right: 0;
  padding: 0.35em 1em;
  text-align: center;
  background-color: rgba(197, 186, 255, 0.7);
}
.message__hide__btn {
  background-color: transparent;
  display: inline-block;
  margin-left: 0.5em;
  border: none;
  font-size: 1.4em;
  font-weight: 500;
  font-family: inherit;
  padding: 0.2em;
  color: var(--info-color);
}
.message__hide__btn:hover, .message__hide__btn:focus {
  cursor: pointer;
}

.message--hide {
  display: none;
}

/************************
FORMS STYLING
***********************/
.form {
  width: 95%;
  max-width: 720px;
  margin: 0 auto;
}
.form__name__group {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 0.5em;
}
.form__group {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-bottom: 1em;
}
.form__group label {
  text-transform: capitalize;
  color: var(--label-color);
  margin-bottom: 0.25em;
  letter-spacing: 0.05em;
  font-size: 1.2em;
}
.form__group input,
.form__group textarea {
  --shadow-color:rgba(0,0,0,.4);
  width: 100%;
  padding: 0.6em 0.7em;
  font-family: "Noto Sans", sans-serif;
  font-size: 1.05em;
  font-weight: 400;
  letter-spacing: 0.75px;
  background-color: var(--input-color);
  border-radius: 0.25em;
  border: none;
  border: 0.1em solid var(--input-color);
  box-shadow: 0 0 3px var(--shadow-color);
  outline: none;
}
.form__group input:focus,
.form__group textarea:focus {
  --shadow-color:rgb(0,0,0);
}
.form__group textarea {
  resize: vertical;
  height: 150px;
}
.form__group span {
  font-size: 0.7em;
  letter-spacing: 0.02em;
  color: #e21515;
}
.form__submit__group {
  margin-bottom: 1em;
}
.form__submit__group input[type=submit] {
  margin-top: 1em;
  text-transform: uppercase;
  background-image: linear-gradient(to top, var(--btn-color-1), var(--btn-color-2));
  color: var(--color-white);
  width: 100%;
  padding: 0.75em;
  font-weight: 500;
  font-family: inherit;
  border: none;
  font-size: 1.1em;
  position: relative;
  border-radius: 0.35em;
  letter-spacing: 0.05em;
  box-shadow: 0 0 4px var(--btn-shadow-color);
}
.form__submit__group input[type=submit]:hover, .form__submit__group input[type=submit]:focus {
  cursor: pointer;
  background-image: linear-gradient(to top, var(--btn-hover-color-1), var(--btn-hover-color-2));
}

.nameForm {
  margin-bottom: 3.5em;
}

.aboutForm {
  padding-top: 2.5em;
  margin-bottom: 3.5em;
  border-top: 2px solid var(--input-border-color);
}

.uniqueForm {
  padding-top: 2.5em;
  border-top: 2px solid var(--input-border-color);
}